import React, { useState } from "react"
import Color from "color"
import getContrastScore from "../utils/getContrastScore"

const ComboColor = ({
  name,
  comboProperty,
  currentCombination,
  pinnedColors,
  onPinColor,
  onClick
}) => {
  const contrastScore = getContrastScore(
    currentCombination[comboProperty],
    "#ffffff"
  )

  let lockColor =
    contrastScore < 4.5
      ? Color(currentCombination[comboProperty]).darken(0.75)
      : Color(currentCombination[comboProperty]).lighten(1.5)
  let outlineColor =
    contrastScore < 1.5
      ? Color(currentCombination[comboProperty]).darken(0.125)
      : "white"

  const onColorClick = () =>
    onClick(currentCombination[comboProperty], comboProperty)

  return (
    <Div
      alignItems="center"
      display="flex"
      css={{ position: "relative" }}
      bg={currentCombination[comboProperty]}
      color={lockColor}
    >
      <Div
        width={64}
        display="flex"
        alignItems="center"
        justifyContent="center"
        py={1}
        mr={2}
        css={{
          cursor: "pointer",
          ":hover > svg": { opacity: 1 }
        }}
        onClick={onPinColor(comboProperty)}
      >
        <Icon
          type="lock"
          color={lockColor}
          size={16}
          mx="auto"
          css={{
            opacity: pinnedColors[comboProperty] ? 1 : .25,
            ":hover": { opacity: 1 }
          }}
        />
      </Div>
      <Div
      >
        <Span color={lockColor} display="block" fontWeight={700}>
          {name}:
        </Span>
        <Span fontSize={3} color={lockColor} css={{ cursor: "pointer" }} onClick={onColorClick}>
          {currentCombination[comboProperty]}
        </Span>
      </Div>
    </Div>
  )
}

const CombinationTools = ({
  currentCombination,
  pinnedColors,
  onPrevious,
  onNext,
  onPinColor,
  onLike,
  onAutoCycling,
  isRunning,
  onColorClick
}) => {
  return (
    <Div>
      <Flex fontSize={1} justifyContent="center">
          <ComboColor
            name="Parent Bg"
            comboProperty="parentBg"
            currentCombination={currentCombination}
            pinnedColors={pinnedColors}
            onPinColor={onPinColor}
            onClick={onColorClick}
          />
          <ComboColor
            name="Color"
            comboProperty="color"
            currentCombination={currentCombination}
            pinnedColors={pinnedColors}
            onPinColor={onPinColor}
            onClick={onColorClick}
          />

          <ComboColor
            name="Bg"
            comboProperty="bg"
            currentCombination={currentCombination}
            pinnedColors={pinnedColors}
            onPinColor={onPinColor}
            onClick={onColorClick}
          />

          <ComboColor
            name="Border"
            comboProperty="borderColor"
            currentCombination={currentCombination}
            pinnedColors={pinnedColors}
            onPinColor={onPinColor}
            onClick={onColorClick}
          />
      </Flex>
    </Div>
  )
}

export default CombinationTools
